import React from 'react';
import { useNavigate } from 'react-router-dom';
import { format } from 'date-fns';

const BlogCard = ({ blog }) => {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate(`/blog/${blog.link}`);
  };

  // Format the date for better readability and SEO
  const formattedDate = blog.uploadDate ? format(new Date(blog.uploadDate), 'MMMM d, yyyy') : '';

  return (
    <article 
      className="max-w-sm w-full h-96 rounded overflow-hidden shadow-lg m-4 cursor-pointer hover:shadow-xl transition-shadow duration-300" 
      onClick={handleClick}
      itemProp="blogPost"
      itemScope 
      itemType="https://schema.org/BlogPosting"
    >
      <link itemProp="url" href={`https://todayeggrates.com/blog/${blog.link}`} />
      
      <div className="h-48 bg-gray-200 flex items-center justify-center">
        <img 
          className="h-full w-full object-cover" 
          src={blog.image} 
          alt={blog.title} 
          itemProp="image"
          loading="lazy"
        />
      </div>
      
      <div className="px-6 py-4 flex flex-col justify-between h-48">
        <div>
          <h3 
            className="font-bold text-xl mb-2 hover:text-blue-600 transition-colors" 
            itemProp="headline"
          >
            {blog.title}
          </h3>
          
          <p 
            className="text-gray-700 text-base line-clamp-3" 
            itemProp="description"
          >
            {blog.description}
          </p>
          
          {blog.category && (
            <span 
              className="inline-block bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded mt-2" 
              itemProp="about"
            >
              {blog.category}
            </span>
          )}
        </div>
        
        <div className="mt-4 flex justify-between items-center">
          <time 
            className="text-gray-500 text-sm" 
            dateTime={blog.uploadDate} 
            itemProp="datePublished"
          >
            {formattedDate}
          </time>
          
          <span className="text-blue-600 text-sm font-medium">Read more →</span>
        </div>
        
        <meta itemProp="author" content="TodayEggRates.com" />
        <meta itemProp="publisher" content="TodayEggRates.com" />
        {blog.keywords && <meta itemProp="keywords" content={blog.keywords} />}
      </div>
    </article>
  );
};

export default BlogCard;